.s3-file-upload-component .s3-file-upload-muted {
  color: #808080;
}
.s3-file-upload-component .s3-file-upload-message {
  width: 100%;
  padding: 5px;
  border: 1px solid;
}
.s3-file-upload-component .s3-file-upload-message.s3-file-upload-message-error {
  border-color: #c50022;
}
.s3-file-upload-component .vue-dropzone {
  border: 2px dashed #c1cad9;
  border-radius: 5px;
  background: #fff;
  padding: 10px;
  height: 68px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.s3-file-upload-component .vue-dropzone.dz-clickable {
  cursor: pointer;
}
.s3-file-upload-component .vue-dropzone.dz-drag-hover {
  box-shadow: 0 0 10px inset #c1cad9;
}
.s3-file-upload-component .vue-dropzone.dz-started .dz-message {
  display: none;
}
.s3-file-upload-component .vue-dropzone .dz-preview .dz-image,
.s3-file-upload-component .vue-dropzone .dz-preview .dz-success-mark,
.s3-file-upload-component .vue-dropzone .dz-preview .dz-error-mark {
  display: none;
}
.s3-file-upload-component .vue-dropzone .dz-preview .dz-details {
  display: flex;
}
.s3-file-upload-component .vue-dropzone .dz-preview .dz-details .dz-size {
  margin-right: 10px;
}
.s3-file-upload-component .vue-dropzone .dz-preview .dz-progress {
  width: 100%;
  height: 5px;
  position: relative;
}
.s3-file-upload-component .vue-dropzone .dz-preview .dz-progress .dz-upload {
  position: absolute;
  top: 0;
  left: 0;
  background: #65ac1e;
  height: 100%;
}
.s3-file-upload-component .s3-file-upload-preview {
  background: #fff;
  border: 1px solid #c1cad9;
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 10px;
}
.s3-file-upload-component .s3-file-upload-preview .s3-file-upload-preview-icon {
  margin-right: 20px;
  font-size: 30px;
}
